// action : {
//   type,
//   payload,
// }
const intitialState = {
  balance: 0
  }
  
  const CounterReducer = (state=intitialState, action) => {
    switch (action.type) {
      case "INCREMENT1000":
        return {
          balance: state.balance + action.values
        }
      case "INCREMENT5000": 
        return {
          balance: state.balance + action.values
        }
      case "DECREMENT1000":
          return{
            balance: state.balance - action.values  
          }
      default:
        return state
    }
  }
  
  export default CounterReducer