import React, {Fragment}  from 'react'
import { useSelector, useDispatch } from 'react-redux'
import ButtonKlik from './Button'
import TitleComponent from './TitleComponent'

export default function CounterAppRedux() {
  const balance = useSelector(state => state.balance)
  const dispatch = useDispatch()

  const save1000 = (value) => {
    dispatch({type: "INCREMENT1000",values: value})
    setTimeout(() => {
      alert(`Terima kasih, saldo anda bertambah ${value}, menjadi ${balance+value}`)
    }, 100)
  }

  const save5000 = (value) => {
    dispatch({type: "INCREMENT5000", values: value})
    setTimeout(() => {
      alert(`Terima kasih, saldo anda bertambah ${value}, menjadi ${balance+value}`)  
    }, 100);
  }

  const withdrw1000 = (value) => {
    const confirmation = window.confirm('apakah anda yakin?')
    if(balance > 0 ){
      if(confirmation){
        dispatch({type: "DECREMENT1000", values: value})
        alert(`Saldo anda berkurang ${value}, sekarang jadi ${balance-value}`)
      } else {
        alert('OK')
      }
    } else {
      alert('Saldo tidak cukup')
    }
  }

  return (
    <Fragment>
      <TitleComponent text={`Saldo anda adalah : Rp. ${balance}`} /> 
      {/* Saldo anda adalah : Rp. {balance} */}
      <br />
      <hr />
      Apakah mau tambah saldo?
      <br />
      {/* <button onClick={() => save1000(1000)}>Tambah Rp.1000</button>
      <button onClick={() => save5000(5000)}>Tambah Rp.5000</button> */}
      <button className="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 m-5 rounded" onClick={() => save1000(1000)}>Tambah Rp.1000</button>
      <button className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 m-5 rounded" onClick={() => save5000(5000)}>Tambah Rp.5000</button>
      <hr />
      Apakah mau tarik saldo?
      <br/>
      {/* <button onClick={() => this.tarikSaldo1000()}>Tarik Rp.1000</button> */}
      <ButtonKlik klik={() => withdrw1000(1000)} text={"Tarik Rp. 1000"} color={"Red"}/>
    </Fragment>
  )
  // const count = useSelector(state => state.count)
  // const dispatch = useDispatch()

  // return (
  //   <div>
  //       Saldo anda adalah : Rp. {count}
  //       <br />
  //       <hr />
  //       Apakah mau tambah saldo?
  //       <br />
  //       <button onClick={() => dispatch({type: "INCREMENT"})}>Tambbah Rp.1000</button>
  //       <button onClick={() => dispatch({type: "INCREMENT5000"})}>Tambbah Rp.5000</button>
  //       <hr />
  //       Apakah mau tarik saldo?
  //       <br/>
  //       <Button klik={() => {alert('Anday yakin?'); count === 0 ? alert('SALDO KOSONG'): dispatch({type: "TARIK"})}} text={"Tarik Rp. 1000"}/>
  //   </div>
    // <div>
    //   dari redux
    //   <h1>{count}</h1>
    //   <button onClick={() => dispatch({type: "INCREMENT"})}>+</button>
    //   <button onClick={() => dispatch({type: "DECREMENT"})}>-</button>
    // </div>
}
